<template>
  <!-- 总体 -->
  <div class="collectivity">

    <!-- 标题 -->
    <div class="title" ref="title">
      <img src="@/assets/favicon.png">
      <span>olmMamChatGpt</span>
    </div>

    <!-- 回答 -->
    <div class="Replyone" ref="Replyone">
      <div class="Reply">
        <general load="false" name="true"></general>
        <general
        v-for="item in content" :key="item.text.question"
        :name="item.name"
        :content="item.text.question"
        :logo="item.text.logo"
        :Goon="item.text.Goon"
        :id="item.text.userid"
        @clickGo="clickGoon"
        ></general>
      </div>
    </div>

    <!-- 用户输入 -->
    <div class="enclosure" ref="enclosure">
      <div class="user" ref="user">
        <div class="textarea" contenteditable="true" @focus="textareafocus" @blur="textareablur" @input="inputone">请输入你的问题!</div>
        <button title="发送" @touchend="clickinitialize">发送</button>
      </div>
    </div>
    
  </div>
</template>

<script setup>
import { nextTick, onMounted, ref } from 'vue';
import general from '@/components/general.vue'
import { initialize } from '@/axios/chatgpt.js'

let content = ref([]);

const title = ref(null)

const user = ref(null);

const enclosure = ref(null)

const Replyone = ref(null)

async function clickinitialize() {
  if(user.value.children[0].innerText.trim() === '请输入你的问题!') {
    return
  } else if(user.value.children[0].innerText.trim() === '') {
    user.value.children[0].innerText = ''
    return
  }
  content.value.push({name:false,text:{userid:1,Goon:false,logo:false,question:user.value.children[0].innerText.trim()}})
  user.value.children[0].innerText = ''
  for(let i = 0 ; i < content.value.length ; i++) {
    if(content.value[i].text.Goon) {
      content.value[i].text.Goon = false;
    }
  }
  let index = content.value.length-1
  content.value.push({name:true,text:{userid:1,Goon:false,logo:true,question:''}})
  nextTick(()=>{
    scrollbuttom()
  })
  let inde = content.value.length-1
  const temp = await initialize(1,content.value[index].text.question)
  if(temp === undefined || temp.ok === 0) {
    content.value[inde].text.question = '网络出现问题!请重新连接.';
    content.value[inde].text.Goon = false;
    content.value[inde].text.logo = false;
    nextTick(()=>{
      scrollbuttom()
    })
  } else {
    for(let i = 0 ; i < content.value.length ; i++) {
      if(content.value[i].text.Goon) {
        content.value[i].text.Goon = false;
      }
    }
    content.value[inde].text.question = temp.data.choices[0].message.content;
    content.value[inde].text.Goon = true;
    content.value[inde].text.logo = false;
    nextTick(()=>{
      scrollbuttom()
    })
  }
}

async function clickGoon(id) {
  for(let i = 0 ; i < content.value.length ; i++) {
    if(content.value[i].text.Goon) {
      content.value[i].text.Goon = false;
    }
  }
  content.value.push({name:true,text:{userid:id+1,Goon:false,logo:true,question:''}});
  nextTick(()=>{
    scrollbuttom()
  })
  let index = content.value.length-1
  let tem = null;
  for(let i = content.value.length-1 ; i >= 0 ; i--) {
    if(!content.value[i].name) {
      tem = content.value[i].text.question;
      break
    }
  }
  const temp = await initialize(id+1,tem)
  if(temp === undefined || temp.ok === 0) {
    content.value[index].text.question = '网络出现问题!请重新连接.';
    content.value[index].text.Goon = false;
    content.value[index].text.logo = false;
    nextTick(()=>{
      scrollbuttom()
    })
  } else {
    content.value[index].text.question = temp.data.choices[0].message.content;
    content.value[index].text.Goon = true;
    content.value[index].text.logo = false;
    nextTick(()=>{
      scrollbuttom()
    })
  }
}

function textareafocus(e) {
  e.target.innerText = '',
  e.target.style.color = 'black';
}

function textareablur(e) {
  e.target.innerText = '请输入你的问题!',
  e.target.style.color = 'rgba(88, 89, 90, 0.735)';
  nextTick(()=>{
    init()
  })
}

function scrollbuttom() {
  let ReHeight = Replyone.value.offsetHeight;
  let RelHeight = Replyone.value.children[0].offsetHeight;
  if(ReHeight - RelHeight < 0) {
    Replyone.value.scrollTop =  RelHeight - ReHeight
  }
}

function inputone() {
  init()
  scrollbuttom()
}

function init() {
  Replyone.value.style.height = document.documentElement.clientHeight - (title.value.offsetHeight + enclosure.value.offsetHeight) + 'px';
  Replyone.value.style.marginTop = title.value.offsetHeight + 'px';
  Replyone.value.style.marginBottom = enclosure.value.offsetHeight + 'px';
}

window.addEventListener('resize',init)

onMounted(()=>{
  init()
})
</script>

<style lang="less" scoped>
// 总体
.collectivity {
  width: 100%;
  height: 100%;
  overflow: hidden;

  // 标题
  .title {
    width: 100%;
    height: 12vw;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgb(200, 216, 239);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;

    img {
      width: 7vw;
      height: 7vw;
    }

    span {
      font-size: 4vw;
      margin-left: 1.5vw;
      color: rgb(0, 140, 255);
    }
  }

  // 回答
  .Replyone {
    width: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    background-color: rgb(235, 241, 247);

    &::-webkit-scrollbar {
      display: none;
    }

    .Reply {
      width: 100%;
      margin: 0 auto;
    }
  }
  // 用户输入
  .enclosure {
    width: 100%;
    min-height: 12vw;
    max-height: 35vw;
    padding: 1%;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(219, 222, 222);
    overflow: hidden;
    border-top: 1px solid rgb(196, 194, 194);

    .user {
      width: 100%;
      min-height: 12vw;
      max-height: 33vw;
      display: flex;
      justify-content: space-between;
      align-content: center;
      transition: height .5s;
      border: 1px solid rgba(168, 164, 164, 0.712);
      border-radius: 5vw;

      .textarea {
        width: 85%; 
        min-height: 12vw; 
        max-height: 33vw;
        outline: 0; 
        font-size: 3vw; 
        word-wrap: break-word;
        overflow-x: hidden;
        overflow-y: auto;
        background-color: rgba(168, 172, 176, 0.457);
        border-radius: 5vw 0 0 5vw;
        padding: 4vw 2vw 3vw 2vw;
        color: rgba(88, 89, 90, 0.735);

        &::-webkit-scrollbar {
          background-color: transparent;
        }
      }

      button {
        width: 15%;
        border: none;
        border-radius: 0 5vw 5vw 0;
        font-size: 4vw;
        background-color: rgb(5, 250, 169);
      }
    }
  }
}
</style>